</template>

<script>

import * as Demo0 from '@mand-mobile/components/src/codebox/demo/cases/demo0.vue'

import * as Demo1 from '@mand-mobile/components/src/codebox/demo/cases/demo1.vue'

import * as Demo2 from '@mand-mobile/components/src/codebox/demo/cases/demo2.vue'

import * as Demo3 from '@mand-mobile/components/src/codebox/demo/cases/demo3.vue'


export default {
  name: 'codebox-demo',
  data() {
    return {
      demos: [
        
        Demo0,
        
        Demo1,
        
        Demo2,
        
        Demo3,
        
      ],
    }
  },
  components: {
    
    'Demo0': Demo0.default,
    
    'Demo1': Demo1.default,
    
    'Demo2': Demo2.default,
    
    'Demo3': Demo3.default,
    
  },
  computed: {
    lang () {
      const r = window.location.search.substr(1).match(new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i'))
      if (r != null) {
        return decodeURIComponent(r[2])
      }
      return 'zh-CN'
    },
    demoProps () {
      return (index, key, defaultVal = '') => {
        let metaInfo = this.demos[index].metaInfo || {}
        metaInfo = metaInfo[this.lang] || metaInfo
        const value = metaInfo ? metaInfo[key] : ''
        return value || defaultVal
      }
    }
  },
}
</script>

